@charset "utf-8";

/* 웹폰트 */
@include webFont(Noto Sans KR, notosanskr, notosanskr100, 100);
@include webFont(Noto Sans KR, notosanskr, notosanskr300, 300);
@include webFont(Noto Sans KR, notosanskr, notosanskrregular, 400);
@include webFont(Noto Sans KR, notosanskr, notosanskr500, 500);
@include webFont(Noto Sans KR, notosanskr, notosanskr700, 700);
@include webFont(Noto Sans KR, notosanskr, notosanskr900, 900);
@include webFont(Roboto, roboto, roboto100, 100);
@include webFont(Roboto, roboto, roboto, 400);
@include webFont(Roboto, roboto, roboto500, 500);

// basic
html {
    scroll-behavior: smooth;
}

body {
    font-family: "Noto Sans KR", "맑은고딕", "Malgun Gothic", sans-serif;
    font-size: 16px;
    font-weight: 400;
    background-color: #fff;
    line-height: 1;
    letter-spacing: -1px;

    @include wordwrap;
}

// common class
.ir {
    overflow: hidden;
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    font-size: 1%;
    z-index: -9999;
    clip: rect(1px, 1px, 1px, 1px);
    clip-path: inset(50%);
}

.is-hidden {
    display: none;
}

#skip {
    a {
        position: absolute;
        left: 0;
        top: -30px;
    }
}

// layout
#wrap {
    overflow: hidden;
    min-width: 320px;
}