@charset "utf-8";

html {
	-webkit-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-ms-text-size-adjust: 100%;
	-webkit-tap-highlight-color: transparent;
}

body {
	margin: 0;
	padding: 0;
}

blockquote,
button,
code,
div,
dl,
dt,
dd,
figure,
fieldset,
form,
p,
pre,
input,
select,
textarea,
table,
th,
td,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
	padding: 0;
	font-family: inherit;
}

ul,
ol,
li {
	list-style: none;
}

li {
	vertical-align: top;
}

fieldset,
img {
	border: 0 none;
}

address,
em {
	font-style: normal;
}

label {
	cursor: pointer;
}

select {
	-webkit-border-radius: 0;
	border-radius: 0;
	text-transform: none;
	font-family: inherit;

	&::-ms-expand {
		display: none;
	}
}
option {
	padding-right: 6px;
}

caption span,
legend span {
	overflow: hidden;
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	font-size: 1%;
	z-index: -9999;
	clip: rect(1px, 1px, 1px, 1px);
	clip-path: inset(50%);
}

table,
td,
tr {
	margin: 0;
	padding: 0;
	border-collapse: collapse;
	border-spacing: 0;
	font-family: inherit;
}

a,
a:active,
a:hover {
	color: inherit;
	text-decoration: none;
}

abbr[title] {
	border-bottom: 1px dotted;
}

hr {
	height: 0;

	@include box-sizing(content-box);
}

pre {
	white-space: pre-wrap;
}

small {
	font-size: 80%;
}

sub,
sup {
	position: relative;
	font-size: 75%;
	line-height: 0;
	vertical-align: baseline;
}

sup {
	top: -.5em;
}

sub {
	bottom: -.25em;
}

button,
input,
textarea {
	overflow: visible;
	font-family: inherit;
	line-height: normal;
	outline: 0 none !important;
}

button,
select {
	text-transform: none;
}

button[disabled],
[type="button"][disabled],
[type="reset"][disabled],
[type="submit"][disabled] {
  	cursor: default;
}

// Remove the inner border and padding in Firefox.
button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
	padding: 0;
	border-style: none;
}

textarea {
	overflow: auto;
	-webkit-border-radius:0; 
  	border-radius: 0;
	resize: vertical;
	-webkit-appearance: none;
}

input[type=button] {
	overflow: visible;

	@include border-radius(0);
	@include box-sizing(content-box);
}

button,
input[type=button],
input[type=reset],
input[type=submit] {
	-webkit-appearance: button;
	cursor: pointer;
}

[type=text],
[type=password],
[type=tel],
[type=number],
[type=search],
[type=email],
[type=url] {
	-webkit-appearance: none;

	@include  border-radius(0);
}

// Correct the odd appearance in Chrome and Safari.
// Correct the outline style in Safari.
[type="search"] {
  	// -webkit-appearance: textfield;
	outline-offset: -2px;
}

// Remove the inner padding in Chrome and Safari on macOS.
[type="search"]::-webkit-search-decoration {
  	-webkit-appearance: none;
}

// Correct the cursor style of increment and decrement buttons in Chrome.
[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  	height: auto;
}

[type=checkbox],
[type=radio] {
	width: 13px;
	height: 13px;
  	padding: 0;

	@include  box-sizing;
}

// Fix placeholder font properties inheritance.
::-webkit-input-placeholder,
:-ms-input-placeholder,
::-ms-input-placeholder,
::placeholder {
	font: inherit;
}